import './AdminFabricante.css'

import { IonSpinner } from '@ionic/react'
import { observer } from 'mobx-react-lite'
import React, { useEffect, useRef, useState } from 'react'
import * as FIcon from 'react-feather'

import { AdminItemField, DashboardModalComponent, LoaderAdmin, Page, SelectorModal } from '../../components'
import { RUTAS } from '../../constants'
import { comprimirImagen, useApiToaster } from '../../helpers-ts'
import { useStores } from '../../models/root-store/root-store-context'
import { Fabricante, PreviewImagenFabricante } from '../../services/api'

const numeral = require('numeral')

type AdminFabricanteUpdateProps = {
  history: any
  match: any
}

export const AdminFabricanteUpdate: React.FC<AdminFabricanteUpdateProps> = observer(({ history, match }) => {
  const rootStore = useStores()
  const { loginStore } = rootStore
  const [Loading, setLoading] = useState(false)
  const [Ready, setReady] = useState(false)
  const [Errors, setErrors] = useState<Fabricante | null>()
  const [Datos, setDatos] = useState<Fabricante | null>()
  const DatosRef = useRef(null as any)
  const Estado = match.params.estado
  const idFabricante = match.params.id
  const DashaboardRef = useRef<any>()
  const [IdInput, setIdInput] = useState('a')
  const [Previews, setPreviews] = useState<PreviewImagenFabricante | null>()
  const apiToaster = useApiToaster()
  const [PorcentajeCompresion, setPorcentajeCompresion] = useState(0)
  const [InputComprimiendo, setInputComprimiendo] = useState('')

  const setup = () => {
    obtenerFabricante(idFabricante)
  }

  React.useEffect(() => {
    setup()
  }, [idFabricante])

  const obtenerFabricante = (fabricante_id: number) => {
    setLoading(true)
    rootStore.environment.api.getFabricante(fabricante_id).then((result) => {
      setLoading(false)
      if (result.kind === 'ok') {
        setDatos(result.fabricante)
        DatosRef.current = result.fabricante
        setPreviews({ imagen_perfil: result.fabricante.imagen_perfil })
        setReady(true)
      }
    })
  }

  const guardar = () => {
    setLoading(true)
    setErrors(null)
    const datos = { ...Datos, comision_empresa: numeral(Datos?.comision_empresa).value() }
    rootStore.environment.api.guardarFabricante(datos).then((result: any) => {
      setLoading(false)
      apiToaster(result, 'Guardado satisfactoriamente', 'guardar')
      if (result.kind === 'ok') {
        const estado = Estado !== undefined ? Estado : result.fabricante.aprobado ? 'moderado' : 'pendiente'
        history.push(RUTAS.ADMIN_FABRICANTE_DETAIL(estado, result.fabricante.id))
      } else if (result.kind === 'bad-data') {
        setErrors(result.errors)
      }
    })
  }

  const setDato = (campo: string, valor: string) => {
    let datos = { ...DatosRef.current }
    // @ts-ignore
    datos[campo] = valor
    // @ts-ignore
    setDatos(datos)
    DatosRef.current = datos
  }

  const dataSelectorModalRubros = (data: any) => {
    setTimeout(() => setDato('rubros', data), 500)
  }

  const setDatoInput = (campo: string, data: any) => {
    setDato(campo, data)
  }

  const seleccionarInput = (id: string) => {
    setIdInput(id)
    DashaboardRef.current.toggle()
  }

  const setPreview = (campo: string, valor: any) => {
    let previews: PreviewImagenFabricante
    // @ts-ignore
    previews = { ...Previews }
    // @ts-ignore
    previews[campo] = valor
    setPreviews(previews)
  }

  const file = async (data: any) => {
    const imagen_comprimida = await comprimirImagen(data, IdInput, compromiendoImagen)
    setInputComprimiendo(IdInput)
    // @ts-ignore
    setDato(IdInput, imagen_comprimida)
  }

  const compromiendoImagen = (porcentaje: number) => {
    setPorcentajeCompresion(porcentaje)
    if (porcentaje === 100) {
      setLoading(false)
      setTimeout(() => {
        setPorcentajeCompresion(0)
        setInputComprimiendo('')
      }, 500)
    } else {
      setLoading(true)
    }
  }

  const filePreview = (data: any) => {
    setPreview(IdInput, data)
    DashaboardRef.current.toggle()
  }

  return (
    <Page login_required admin>
      <LoaderAdmin visible={Loading} />
      <DashboardModalComponent ref={DashaboardRef} id_input={IdInput} file={file} file_preview={filePreview} />
      {Ready && (
        <div className="page-wrapper" style={{ display: 'flex', height: '100vh', overflowY: 'scroll' }}>
          <div className="container-fluid background-white">
            <div className="row page-titles">
              <div className="col-md-5 col-12 align-self-center">
                <h3 className="text-themecolor">{Datos && Datos.id ? 'Editar fabricante' : 'Crear fabricante'}</h3>
              </div>
              <div className="col-md-7 col-12 align-self-center d-none d-md-block">
                <ol className="breadcrumb mb-0 p-0 bg-transparent float-right">
                  <li className="breadcrumb-item">
                    <a onClick={() => history.push('/admin/')}>Inicio</a>
                  </li>
                  {loginStore.administrador && (
                    <li className="breadcrumb-item">
                      <a onClick={() => history.push('/admin/fabricante/' + Estado)}>Fabricantes</a>
                    </li>
                  )}
                  {loginStore.fabricante && (
                    <li className="breadcrumb-item active">
                      <a onClick={() => history.push('/admin/fabricante/' + Estado + '/detail')}>
                        {Datos && Datos.nombre}
                      </a>
                    </li>
                  )}
                  <li className="breadcrumb-item active">{Datos && Datos.id ? 'Editar' : 'Crear'}</li>
                </ol>
              </div>
            </div>
            <div className="card mb-0">
              <div className="card-body">
                <div className={'row mb-5'}>
                  <div className={'col-3'}>
                    <div>
                      <label className={'font-weight-bold text-primary mt-2'}>
                        Imagen de perfil{' '}
                        {InputComprimiendo === 'imagen_perfil' && PorcentajeCompresion > 0
                          ? PorcentajeCompresion + '%'
                          : ''}
                      </label>
                      <button
                        className={'ml-4 btn btn-light btn-sm'}
                        title={'Subir el logo de la empresa'}
                        onClick={() => seleccionarInput('imagen_perfil')}
                      >
                        <FIcon.Camera size={14} />
                      </button>
                      {Errors && Errors.imagen_perfil && <div className={'text-danger'}>{Errors.imagen_perfil}</div>}
                    </div>
                    {Previews && Previews['imagen_perfil'] && (
                      <img
                        className={'my-3'}
                        src={Previews['imagen_perfil']}
                        style={{ maxHeight: 200, maxWidth: 200 }}
                        alt={'Imagen de perfil de la empresa'}
                      />
                    )}
                  </div>
                  <AdminItemField
                    deshabilitado={!Loading}
                    titulo={'Nombre'}
                    tipo={'texto'}
                    campo={'nombre'}
                    valor={Datos ? Datos.nombre : ''}
                    error={Errors && Errors.nombre}
                    set_dato={setDatoInput}
                    columnas={12}
                    columnas_sm={3}
                  />
                  <AdminItemField
                    deshabilitado={!Loading}
                    titulo={'Reseña de la empresa'}
                    tipo={'ckeditor-full'}
                    campo={'resena_empresa'}
                    valor={Datos ? Datos.resena_empresa : ''}
                    error={Errors && Errors.resena_empresa}
                    set_dato={setDatoInput}
                    columnas={12}
                  />
                  <AdminItemField
                    deshabilitado={!Loading}
                    titulo={'Razón social'}
                    tipo={'texto'}
                    campo={'razon_social'}
                    valor={Datos ? Datos.razon_social : ''}
                    error={Errors && Errors.razon_social}
                    set_dato={setDatoInput}
                    columnas={12}
                    columnas_sm={3}
                  />
                  <AdminItemField
                    deshabilitado={!Loading}
                    titulo={'RUC'}
                    tipo={'texto'}
                    campo={'ruc'}
                    valor={Datos ? Datos.ruc : ''}
                    error={Errors && Errors.ruc}
                    set_dato={setDatoInput}
                    columnas={12}
                    columnas_sm={3}
                  />

                  <AdminItemField
                    deshabilitado={!Loading}
                    titulo={'Número de celular'}
                    tipo={'texto'}
                    campo={'nro_telefono'}
                    valor={Datos ? Datos.nro_telefono : ''}
                    error={Errors && Errors.nro_telefono}
                    set_dato={setDatoInput}
                    columnas={12}
                    columnas_sm={3}
                  />
                  <div className={'col-3'}>
                    <SelectorModal
                      titulo={'Rubros'}
                      titulo_label={'Rubros'}
                      listado_elementos={rootStore.rubros}
                      listado_elementos_seleccionados={Datos ? Datos.rubros : []}
                      datos_filtrados={dataSelectorModalRubros}
                      multiple={true}
                      llave_label_opcion={'nombre'}
                    />
                    {Errors && Errors.rubros && <div className={'text-danger'}>{Errors.rubros}</div>}
                  </div>
                  {loginStore.administrador && (
                    <AdminItemField
                      titulo={'Margen'}
                      tipo={'decimales'}
                      campo={'comision_empresa'}
                      valor={Datos ? Datos.comision_empresa : ''}
                      error={Errors && Errors.comision_empresa}
                      set_dato={setDatoInput}
                      columnas={12}
                      columnas_sm={3}
                      help_text={'Margen en porcentaje aplicado a todos los productos del fabricante'}
                    />
                  )}
                  {loginStore.administrador && (
                    <AdminItemField
                      titulo={'Aprobado'}
                      tipo={'boolean'}
                      campo={'aprobado'}
                      valor={Datos ? Datos.aprobado : ''}
                      set_dato={setDatoInput}
                      columnas={3}
                    />
                  )}
                </div>
                <div className={'form-group text-right'}>
                  <a onClick={() => history.goBack()} className={'btn btn-light'}>
                    Cancelar
                  </a>
                  <button className={'btn btn-primary ml-2'} disabled={Loading} onClick={guardar}>
                    {Loading && <IonSpinner name="crescent" />}
                    {!Loading && <span>Guardar</span>}
                  </button>
                </div>
              </div>
            </div>
          </div>
        </div>
      )}
    </Page>
  )
})

export default AdminFabricanteUpdate
